<template>
    <div class="main-div">
        divisionOptions: {{this.divisionOptions}}
      <br />
      <b-row>
        <div class="center">
          <h5 class="center">Reports list</h5>
        </div>
      </b-row>
      <br />
      <b-row>
        <b-col cols="8">
          <b-row>
            <div class="table-div">
              <b-container fluid>
                <b-table
                  striped
                  class="table table-bordered table-sm"
                  thead-class="thead-light"
                  selectable
                  :select-mode="selectMode"
                  selected-variant="warning"
                  @row-selected="onRowSelected"
                  :items="reportItems"
                  :fields="fields"
                  :current-page="currentPage"
                  :per-page="perPage"
                  :filter="filter"
                  :sort-by.sync="sortBy"
                  :sort-desc.sync="sortDesc"
                  :sort-direction="sortDirection"
                  :hover="true"
                >
                  <template v-slot:cell(selected)="{ rowSelected }">
                    <template v-if="rowSelected">
                      <span class="sr-only">Selected</span>
                    </template>
                    <template v-else>
                      <span class="sr-only">Not selected</span>
                    </template>
                  </template>
                </b-table>
                <b-pagination
                  v-model="currentPage"
                  :total-rows="totalRows"
                  :per-page="perPage"
                  size="sm"
                  class="my-0"
                ></b-pagination>
              </b-container>
            </div>
          </b-row>
        </b-col>
        <b-col cols="4">
          <b-row>
            <b-form-row v-show="dateParameter">
              <b-col cols="4">
                <b-form-group
                  label="From Date"
                  label-for="rptFromDate"
                  label-align="left"
                >
                  <b-input-group>
                    <datepicker
                      placeholder="Select Date"
                      v-model="rptFromDate"
                      format="dd/MM/yyyy"
                      input-class="form-control form-control-sm styleLine"
                    ></datepicker>
                  </b-input-group>
                </b-form-group>
              </b-col>
              <b-col cols="6">
                <b-form-group
                  label="To Date"
                  label-for="rptToDate"
                  label-align="left"
                >
                  <b-input-group>
                    <datepicker
                      placeholder="Select Date"
                      v-model="rptToDate"
                      format="dd/MM/yyyy"
                      input-class="form-control form-control-sm styleLine"
                    ></datepicker>
                  </b-input-group>
                </b-form-group>
              </b-col>
            </b-form-row>
            <b-form-row v-show="divisionParameter">
              <b-col cols="6">
                <b-form-group
                  label="Division"
                  label-for="divCode"
                  label-align="left"
                  class="mb-1 textBoxWidth"
                >
                  <b-input-group>
                    <b-form-select
                    v-model="divCode"
                    class="form-control form-control-sm styleLine mb-1 textBoxWidth"
                    style="padding-left: 1px"
                  >
                    <option value="101">PRESS</option>
                    <option value="102">PUBLISHING</option>
                  </b-form-select>
                  </b-input-group>
                </b-form-group>
              </b-col>
              <b-col cols="6" class="col-left">
                <!-- Add any other form elements or labels here if needed -->
              </b-col>
            </b-form-row>
            <!-- Add other form rows as needed -->
          </b-row>
          <b-row>
            <b-col cols="4">
              <b-button variant="secondary" @click="runReport">
                {{ "Show Report" }}
              </b-button>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
      <hr />
      <div v-show="isBusy">
        <template>
          <div class="text-center text-danger my-2">
            <b-spinner class="align-middle"></b-spinner>
            <h4><strong>Please wait...</strong></h4>
          </div>
        </template>
      </div>
    </div>
  </template>

<script>
import axios from 'axios'
import { mapGetters } from 'vuex'
import Datepicker from 'vuejs-datepicker'

export default {
  data () {
    return {
      isBusy: false,
      selectMode: 'single',
      selectedReport: '',
      rptFromDate: '',
      rptToDate: '',
      reportItems: [],
      dateParameter: false,
      divCode: '101', // Default value
      empCode: 0,
      department: {},
      departmentList: [],
      divisionParameter: false,
      employeeParameter: false,
      payrollHistoryParameter: false,
      payrollYear: 0,
      payrollMonth: 0,
      tRows: 0,
      selectedValue: null,
      searchDept: {
        attr1: { name: 'id', label: 'Department Code' },
        attr2: { name: 'departmentName', label: 'Department Name' },
        attr3: { name: 'divisionName', label: 'Division Name' }
      },
      fields: [
        {
          key: 'reportId',
          label: 'Report Id',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-left'
        },
        {
          key: 'reportDescription',
          label: 'Report Description',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-left',
          tdClass: 'col-width-300'
        }
      ],
      currentPage: 1,
      perPage: 10,
      totalRows: 0,
      sortBy: null,
      sortDesc: false,
      sortDirection: 'asc',
      filter: null
    }
  },
  computed: {
    ...mapGetters([])
  },
  components: {
    Datepicker
  },
  mounted () {
    this.getReportList()
  },
  methods: {
    async getReportList () {
      const url = process.env.VUE_APP_API_URL + '/reports'
      try {
        const response = await axios.get(url, {
          headers: {
            'Content-Type': 'application/json'
          }
        })
        this.reportItems = response.data
        this.totalRows = this.reportItems.length
      } catch (error) {
        console.error('Error:', error.response)
      }
    },
    onRowSelected (x) {
      this.selectedReport = x[0].reportName

      if (typeof x[0] !== 'undefined') {
        switch (this.selectedReport) {
          case 'prs1':
            this.reSetParameterDisplay()
            break
          case 'prs1_division':
            this.reSetParameterDisplay()
            this.divisionParameter = true
            break
          case 'prs_department_analysis':
            this.reSetParameterDisplay()
            break
          case 'llo':
            this.reSetParameterDisplay()
            break
          case 'pslip-i':
            this.reSetParameterDisplay()
            this.employeeParameter = true
            break
          case 'pslip-all':
            this.reSetParameterDisplay()
            break
          case 'pslipHistory-i':
            this.reSetParameterDisplay()
            this.employeeParameter = true
            this.payrollHistoryParameter = true
            break
          case 'emp_salary_increment':
            this.reSetParameterDisplay()
            this.dateParameter = true
            break
          case 'emp_salary_increment_i':
            this.reSetParameterDisplay()
            this.employeeParameter = true
            break
          case 'emp_in_service':
            this.reSetParameterDisplay()
            break
          case 'emp_joined_currentyear':
            this.reSetParameterDisplay()
            this.dateParameter = true
            break
          case 'emp_leftservice':
            this.reSetParameterDisplay()
            break
          case 'emp_leftservice_currentyear':
            this.reSetParameterDisplay()
            this.dateParameter = true
            break
          default:
            break
        }
      }
    },
    reSetParameterDisplay () {
      this.dateParameter = false
      this.departmentParameter = false
      this.employeeParameter = false
      this.divisionParameter = false
      this.payrollHistoryParameter = false
    },
    runReport () {
      this.isBusy = !this.isBusy
      const x = this.selectedReport
      let fmDate = ''
      let toDate = ''

      if (!this.errors.length) {
        switch (x) {
          case 'prs1':
          case 'prs_department_analysis':
          case 'llo':
          case 'llo-new':
          case 'pslip-all':
          case 'emp_in_service':
          case 'emp_leftservice':
            window.open(
              process.env.VUE_APP_REPORT_URL +
                  `${x}&rs:Command=Render&rs:format=PDF`
            )
            break
          case 'prs1_division':
            window.open(
              process.env.VUE_APP_REPORT_URL +
                  `${x}&rs:Command=Render&rs:format=PDF&divCode=${this.divCode}`
            )
            break
          case 'pslip-i':
            window.open(
              process.env.VUE_APP_REPORT_URL +
                  `${x}&rs:Command=Render&rs:format=PDF&empCode=${this.empCode}`
            )
            break
          case 'pslipHistory-i':
            window.open(
              process.env.VUE_APP_REPORT_URL +
                  `${x}&rs:Command=Render&rs:format=PDF&empCode=${this.empCode}&trnMonth=${this.payrollMonth}&trnYear=${this.payrollYear}`
            )
            break
          case 'emp_salary_increment':
          case 'emp_joined_currentyear':
          case 'emp_leftservice_currentyear':
            fmDate = this.formatDate(this.rptFromDate)
            toDate = this.formatDate(this.rptToDate)
            window.open(
              process.env.VUE_APP_REPORT_URL +
                  `${x}&rs:Command=Render&rs:format=PDF&fmDate=${fmDate}&toDate=${toDate}`
            )
            break
          case 'emp_salary_increment_i':
            window.open(
              process.env.VUE_APP_REPORT_URL +
                  `${x}&rs:Command=Render&rs:format=PDF&empCode=${this.empCode}`
            )
            break
          default:
            break
        }
      } else {
        alert(this.errors)
      }
      this.isBusy = !this.isBusy
    },
    formatDate (d) {
      var n = d.getMonth() + 1
      return d.getFullYear() + '-' + n + '-' + d.getDate()
    }
  }
}
</script>

  <style scoped>
  /* Add your component-specific styles here */
  .main-div {
    width: 95%;
    margin: auto;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
      Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    font-size: medium;
  }
  .table-div {
    width: 70%;
    margin: auto;
    font-size: medium;
  }
  .col-left {
    text-align: left;
  }
  .col-right {
    text-align: right;
  }
  .input-required {
    background-color: rgb(245, 242, 212);
  }

  #b-col {
    text-align: left;
  }

  .textBoxWidth {
    width: 160px;
  }
  .cellColor {
    background-color: #d9dcdf;
  }
  .calculated-value {
    background-color: #d9dcdf;
  }
  .sub-head {
    background-color: #d9dcdf;
  }
  .group1-color {
    background-color: rgb(242, 242, 242);
  }
  .group2-color {
    background-color: rgb(230, 230, 230);
  }
  p {
    margin-bottom: 0px;
  }
  .spacer {
    margin-top: 5px;
    margin-bottom: 5px;
  }
  .col-width-300 {
    width: 300px;
  }
  .center {
    margin-left: auto;
    margin-right: auto;
  }
  </style>
